*,
*::before,
*::after {
  box-sizing: border-box;
}

.contacts {
  padding-bottom: 30px;

  background-image: linear-gradient(
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0) 25%,
    #84a178 25%
  );

  @media screen and (min-width: 768px) {
    padding-bottom: 20px;

    background-image: linear-gradient(
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0)48%,
      #84a178 48%
    );
  }

  @media screen and (min-width: 1200px) {
    padding-bottom: 60px;

    background-image: linear-gradient(
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0) 18%,
      #84a178 18%
    );
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.contacts-container {
  margin-left: auto;
  margin-right: auto;

  @media screen and(max-width:767px) {
    max-width: 480px;
    padding-left: 20px;
    padding-right: 20px;
  }

  @media screen and(min-width: 768px) {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }

  @media screen and(min-width: 1200px) {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.contacts__list {
  padding: 0;

  @media screen and(max-width: 767px) {
    margin-bottom: 20px;
  }
  @media screen and(min-width: 768px) {
    display: flex;
    gap: 16px;
    margin-bottom: 40px;
  }

  @media screen and(min-width: 1200px) {
    gap: 32px;
    margin-bottom: 60px;
  }
}

.location-card {
  width: 100%;
  position: relative;
  z-index: 1;
  background-color: #fcfcfc;
  padding: 40px 32px 24px 32px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 24px;

  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);

  @media screen and(max-width: 767px) {
    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  @media screen and(max-width:1199px) {
    max-width: 280px;
  }

  @media screen and(min-width: 768px) {
    flex-basis: calc((100% - 32px) / 3);
  }

  @media screen and(min-width: 1200px) {
    flex-basis: calc((100% - 64px) / 3);
    padding: 40px;
  }
}

.location-card__type {
  font-weight: 500;
  font-size: 12px;
  line-height: calc(14 / 12);
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: #131313;
  background-color: #f8d24e;

  display: inline-block;
  padding: 4px 22px;
  margin-bottom: 20px;
  border-radius: 6px;

  @media screen and (min-width: 768px) {
    font-size: 16px;
    line-height: calc(22 / 16);

    padding: 4px 13px;
    margin-bottom: 32px;
  }

  @media screen and (min-width: 768px) {
    padding: 4px 14px;
    margin-bottom: 36px;
  }
}

.location-card__city {
  font-weight: 500;
  font-size: 12px;
  line-height: calc(22 / 12);
  letter-spacing: 0.04em;
  color: #131313;
  margin-bottom: 8px;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: calc(26 / 14);

    margin-bottom: 12px;
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: calc(30 / 16);

    margin-bottom: 8px;
  }
}

.location-card__address {
  padding-bottom: 24px;
  border-bottom: solid 1px #e1e1e1;
  margin-bottom: 24px;

  @media screen and (min-width: 768px) {
    padding-bottom: 32px;
    margin-bottom: 32px;
  }
}

.address__link {
  display: block;
  font-weight: 500;
  font-size: 12px;
  line-height: calc(22 / 12);
  letter-spacing: 0.04em;
  font-style: normal;

  color: #d6936d;

  &:hover,
  &:focus {
    text-decoration: underline;
    cursor: url(../images/product/pointer.png), pointer;
  }

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: calc(26 / 14);
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: calc(29 / 16);
  }
}

.location-card__work-time {
  &:not(:last-child) {
    margin-bottom: 8px;
  }
}

.location-card__days {
  font-weight: 500;
  font-size: 12px;
  line-height: calc(22 / 12);

  color: #131313;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: calc(26 / 14);
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: calc(29 / 16);
  }
}

.location-card__hours {
  font-weight: 500;
  font-size: 12px;
  line-height: calc(22 / 12);
  color: #ababab;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: calc(26 / 14);
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: calc(29 / 16);
  }
}

.location-card__mode {
  padding-left: 0;
  padding-bottom: 24px;
  margin-bottom: 24px;
  border-bottom: solid 1px #e1e1e1;

  @media screen and (min-width: 768px) {
    padding-bottom: 32px;
    margin-bottom: 32px;
  }
}

.location-card__tel {
  font-weight: 500;
  font-style: normal;
  font-size: 12px;
  line-height: 19/12;

  color: #131313;

  display: block;
  margin-bottom: 6px;
  cursor: url(../images/product/pointer.png), pointer;

  &:hover,
  &:focus {
    text-decoration: underline;
  }

  @media screen and (min-width: 768px) {
    margin-bottom: 4px;
    font-size: 14px;
    line-height: calc(26 / 14);
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: calc(24 / 16);
  }
}

.location-card__mail {
  font-weight: 500;
  font-style: normal;
  font-size: 12px;
  line-height: 19/12;

  color: #131313;

  cursor: url(../images/product/pointer.png), pointer;

  &:hover,
  &:focus {
    text-decoration: underline;
  }

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: calc(26 / 14);
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: calc(24 / 16);
  }
}

/*Buttons*/

.footer_btn-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 0;
  margin-left: auto;
  margin-right: auto;
  color: $second-color-text;

  @media screen and (max-width: 767px) {
    display: block;
    text-align: center;
    max-width: 200px;
  }

  @media screen and (min-width: 768px) {
    height: 42px;
  }
}

.footer-locations__button {
  padding: 10px 38px;

}

.footer-franchise__button {
  padding: 10px 53px;
  border: 2px solid #f8d24e;
  background-color: transparent;

  @media screen and (max-width: 767px) {
    margin-top: 16px;
  }

  @media screen and (min-width: 768px) {
    margin-left: 16px;
  }
}

.icon-arrow-right {
  margin-left: 4px;
  width: 12px;
  height: 12px;
  fill: #000;

  transition-property: fill;
  transition-duration: 250ms;
  transition-timing-function: $timings;
}

.footer-locations__button:focus .icon-arrow-right,
.footer-franchise__button:focus .icon-arrow-right {
  @media screen and (min-width: 1200px) {
    fill: $main-color-text;
  }
}
